import { useState } from 'react'
import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField'
import Grid from '@mui/material/Grid'
import Backdrop from '@mui/material/Backdrop'
import Typography from '@mui/material/Typography';
import ResourcesCalculator from './ResourcesCalculator.js'
import Alert from '@mui/material/Alert';
import { Loader, LoaderContext } from './Loader.js'
import { createTheme, ThemeProvider } from '@mui/material/styles';

const theme = createTheme({
  palette: {
    mode: 'dark',
  },
})

function AskWallet({ setWallet }) {
  return (
    <Grid
      container
      justify="center"
      alignItems="center"
      direction="column"
      spacing={5}
    >
      <Grid item>
        <Typography variant="h5" color="primary">
          Type your wax wallet and hit enter
        </Typography>
      </Grid>
      <Grid item>
        <TextField
          variant="outlined"
          label="Wax wallet"
          fullWidth
          style={{ marginBottom: "1em" }}
          onKeyPress={(e) => {
            if (e.key === "Enter") {
              setWallet(e.target.value)
            }
          }}
        />
      </Grid>
    </Grid>
  )
}

function App() {
  const [wallet, setWallet] = useState()
  const [loading, setLoading] = useState(false)

  return <ThemeProvider theme={theme}>
    <Box>
      <LoaderContext.Provider value={[loading, setLoading]}>
        <Backdrop
          sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
          open={loading}
        >
          <Loader />
        </Backdrop>
        <>
          <AskWallet setWallet={setWallet} />
          <ResourcesCalculator wallet={wallet} />
        </>
      </LoaderContext.Provider>
    </Box>
  </ThemeProvider>
}

export default App
