import _ from 'lodash'
import { useEffect, useState, useMemo, useContext } from 'react'
import { ExplorerApi } from "atomicassets"
import Tooltip from '@mui/material/Tooltip'
import Typography from '@mui/material/Typography'
import Paper from '@mui/material/Paper'
import Box from '@mui/material/Box'
import Grid from '@mui/material/Grid'
import { Done as DoneIcon, ErrorOutline as ErrorOutlineIcon  } from '@mui/icons-material'
import WellbeingIcon from './images/resources/wellbeing.webp'
import EngineeringIcon from './images/resources/engineering.webp'
import OxygenIcon from './images/resources/oxygen.webp'
import WaterIcon from './images/resources/water.webp'
import FoodIcon from './images/resources/food.webp'
import ScienceIcon from './images/resources/science.webp'
import FuelIcon from './images/resources/fuel.webp'
import PowerIcon from './images/resources/power.webp'
import DataIcon from './images/resources/data.webp'
import MedicineIcon from './images/resources/medicine.webp'
import SocialIcon from './images/resources/social.webp'
import RegolithIcon from './images/resources/regolith.webp'
import MartiaIcon from './images/resources/martia.webp'
import RepairsIcon from './images/resources/repairs.webp'
import ConstructionMaterialIcon from './images/resources/construction_material.webp'
import LogisticIcon from './images/resources/logistic.webp'
import { LoaderContext } from './Loader.js'
import BaseTable, { AutoResizer } from 'react-base-table'

const atomic = new ExplorerApi("https://wax.api.atomicassets.io", "atomicassets", { fetch })

const sleep = (ms) => {
  return new Promise(resolve => setTimeout(resolve, ms))
}

const RESOURCE_CHAIN = {
  'Solar Panel Array': { power: 1 },
  'Fission Reactor': { power: 2 },
  'Megapower Reactor': { power: 3 },
  'Ice Mining Rig': { power: -1, water: 2 },
  'Inflatable Sleeping Pod': { power: -1, wellbeing: 2 },
  'Rover': { martia: 2, power: -1 },
  'Scouting Drone': { martia: 2, power: -1 },
  'Buggy': { martia: 2, power: -1 },
  'Fuel Plant': { power: -1, water: -2, oxygen: 1, fuel: 1 },
  '3D Printing Robot': { power: -1, regolith: -2, construction_materials: 2 },
  'Inflatable Greenhouse': { power: -1, water: -2, food: 1 },
  'Male Engineer': { water: -1, wellbeing: -2, food: -1, oxygen: -1, engineer_labor: 3 },
  'Female Engineer': { water: -1, wellbeing: -2, food: -1, oxygen: -1, engineer_labor: 3 },
  'Male Scientist': { water: -1, wellbeing: -2, food: -1, oxygen: -1, science_labor: 3 },
  'Female Scientist': { water: -1, wellbeing: -2, food: -1, oxygen: -1, science_labor: 3 },
  'Male Botanist': { water: -1, wellbeing: -2, food: 0, oxygen: -1, science_labor: 3 },
  'Female Botanist': { water: -1, wellbeing: -2, food: 0, oxygen: -1, science_labor: 3 },
  'Male Doctor': { water: -1, wellbeing: -2, food: -1, oxygen: -1, social_labor: 2, medicine: 1 },
  'Female Doctor': { water: -1, wellbeing: -2, food: -1, oxygen: -1, social_labor: 2, medicine: 1 },
  'Inflatable Laboratory': { power: -2, science_labor: -2, data: 3 },
  'Fuel Generator': { power: -2, water: -3, oxygen: 1, fuel: 2, engineer_labor: -1 },
  'Inflatable Dining Hall': { power: -2, wellbeing: 2, food: -2, social_labor: -1 },
  'Inflatable Social Hub': { power: -2, water: -3, wellbeing: 2, social_labor: -1 },
  'Medical Bay': { power: -2, social_labor: -1, medicine: 3 },
  'Communication Array': { power: -3, science_labor: -1, data: 3 },
  'Cargo Rocketship': { construction_materials: 2, engineer_labor: -2, logistic_labor: -2, repairs: 2 },
  'Crew Rocketship': { construction_materials: 2, engineer_labor: -2, logistic_labor: -2, medicine: 2 },
}

const RARITY_STRENGTH = {
  'Dark Matter': 465,
  'Gold': 144,
  'Stainless Steel': 34,
  'Shadow': 6.5,
  'Standard': 1,
}

const assetToCard = (asset) => {
  const { rarity } = asset.data

  return {
    name: asset.name,
    rarity,
    strength: RARITY_STRENGTH[rarity]
  }
}

const getCards = async ({ wallet }) => {
  console.time('fetch_cards')
  let assets = []

  const options = {
    owner: wallet,
    collection_name: 'colonizemars',
    schema_name: 'missionone,missiontwo,missionthree'
  }
  let page = 1
  while(true) {
    try {
      const resp = await atomic.getAssets(options, page, 500)
      await sleep(100)
      if (resp.length === 0) break
      assets.push(...resp)
      page = page + 1
    } catch (error) {
      console.error(error)
      await sleep(5000)
    }
  }

  console.log("All cards received!")

  const filteredAssets = assets.filter(asset => _.keys(RARITY_STRENGTH).includes(asset.data.rarity))
  const mappedAssets = filteredAssets.map(asset => assetToCard(asset))

  console.timeEnd('fetch_cards')
  return mappedAssets
}

const performSimulation = (cards) => {
  let balance = {}

  console.time('sort_cards')
  const sortedCards = _.orderBy(cards, [
    (card) => _.keys(RESOURCE_CHAIN).indexOf(card.name),
    (card) => card.strength,
  ], ['asc', 'desc'])
  console.timeEnd('sort_cards')

  const getRes = (name) => {
    if (balance[name] === undefined) {
      balance[name] = 0.0
    }
    return balance[name]
  }

  console.time('simulation')
  const processedCards = sortedCards.map(card => {
    const cardResources = RESOURCE_CHAIN[card.name]
    const requiredResources = _.entries(cardResources).filter(([resource, quantity]) => quantity < 0.0).map(([resource, quantity]) => [resource, -quantity * card.strength])
    const producedResources = _.entries(cardResources).filter(([resource, quantity]) => quantity > 0.0).map(([resource, quantity]) => [resource, quantity * card.strength])

    let used = false

    if (_.every(requiredResources, ([resource, quantity]) => getRes(resource) >= quantity)) {
      used = true

      for (let [resource, quantity] of requiredResources) {
        balance[resource] = getRes(resource) - quantity
      }
      for (let [resource, quantity] of producedResources) {
        balance[resource] = getRes(resource) + quantity
      }
    }

    return {
      ...card,
      used,
      requiredResources,
      producedResources,
      currentResources: _.entries(balance)
    }
  })

  console.timeEnd('simulation')

  const usedStrength = processedCards.filter(card => card.used).reduce((sum, card) => sum + card.strength, 0)
  const totalStrength = processedCards.reduce((sum, card) => sum + card.strength, 0)
  const score = usedStrength / totalStrength

  return {
    cards: processedCards,
    balance,
    usedStrength,
    totalStrength,
    score
  }
}

function ResourceBalance ({ name, quantity, display = 'block' }) {
  const icons = {
    'engineer_labor': EngineeringIcon,
    'wellbeing': WellbeingIcon,
    'oxygen': OxygenIcon,
    'water': WaterIcon,
    'food': FoodIcon,
    'science_labor': ScienceIcon,
    'fuel': FuelIcon,
    'power': PowerIcon,
    'data': DataIcon,
    'medicine': MedicineIcon,
    'regolith': RegolithIcon,
    'social_labor': SocialIcon,
    'martia': MartiaIcon,
    'repairs': RepairsIcon,
    'logistic_labor': LogisticIcon,
    'construction_materials': ConstructionMaterialIcon,
  }

  const src = icons[name]

  return <Box display={display} sx={{ margin: '0.3rem' }}>
    <img alt={name} title={name} width="15px" height="15px" src={src} /> {quantity}
  </Box>
}

function ResourcesBalance({ resources, display = 'block' }) {
  return resources.map(([name, quantity], idx) => <ResourceBalance key={idx} name={name} quantity={quantity} display={display} /> )
}

function CardsTable ({ cards }) {
  const balanceRenderer = ({ cellData }) => {
    return <ResourcesBalance resources={cellData} display="inline" />
  }

  const outputRenderer = ({ rowData, cellData }) => {
    return rowData.used ?
      <ResourcesBalance resources={cellData} display="inline" /> :
      <Tooltip title= "Not enough resources to use this item"><ErrorOutlineIcon sx={{ color: 'red' }} /></Tooltip>
  }

  const columns = [
    { title: 'Name', key: 'name', dataKey: 'name', width: 150, resizable: true },
    { title: 'Rarity', key: 'rarity', dataKey: 'rarity', width: 150, resizable: true },
    { title: 'Input', key: 'requiredResources', dataKey: 'requiredResources', width: 150, resizable: true, cellRenderer: balanceRenderer },
    { title: 'Output', key: 'producedResources', dataKey: 'producedResources', width: 150, resizable: true, cellRenderer: outputRenderer },
    { title: 'Balance', key: 'currentResources', dataKey: 'currentResources', resizable: true, cellRenderer: balanceRenderer }
  ]
  const data = cards.map((card, id) => ({ ...card, id }))

  return <AutoResizer>
    {
      ({ width, height }) => (
        <BaseTable data={data} width={width} height={ window.innerHeight - 200 } columns={columns} />
      )
    }
  </AutoResizer>

}

function SimulationResult ({ simulation }) {
  return <Box sx={{ padding: '10px' }}>
    <Grid container spacing={2}>
      <Grid item xs={1}>
        <Paper sx={{ padding: '0.2rem' }}>
          <Typography sx={{ textAlign: 'center' }}>Earnings</Typography>
          <ResourcesBalance resources={_.entries(simulation.balance)} />
        </Paper>

        <Paper sx={{ padding: '0.2rem', marginTop: '1rem' }}>
          <Typography sx={{ textAlign: 'center' }}>Efficiency</Typography>
          <Typography sx={{ textAlign: 'center' }}>{ (simulation.score * 100).toFixed(2) }%</Typography>
        </Paper>
      </Grid>
      <Grid item xs={11}>
        <CardsTable cards={simulation.cards} />
      </Grid>
    </Grid>
  </Box>
}

function ResourcesCalculator({ wallet }) {
  const [loading, setLoading] = useContext(LoaderContext)
  const [simulation, setSimulation] = useState()

  useEffect(() => {
    if (!wallet) return

    setLoading(true)

    const req = async () => {
      console.log("START")
      const cards = await getCards({ wallet })
      setSimulation(performSimulation(cards))
      setLoading(false)
    }

    req()
  }, [wallet])

  return simulation ? <SimulationResult simulation={simulation} /> : null
}

export default ResourcesCalculator
